/**
 * Initialize the VCC actions module.
 */
export const init = () => {
    setupDeleteConfirmation();
};

/**
 * Setup delete confirmation handlers using event delegation.
 */
const setupDeleteConfirmation = () => {
    // Use event delegation on the document body to handle dynamically loaded content
    document.addEventListener('click', (event) => {
        const deleteButton = event.target.closest('.vcc-delete-action');

        if (!deleteButton) {
            return;
        }

        event.preventDefault();

        const confirmMessage = deleteButton.dataset.confirmMessage;
        if (!confirmMessage) {
            // If no confirm message, proceed directly
            window.location.href = deleteButton.href;
            return;
        }

        if (confirm(confirmMessage)) {
            window.location.href = deleteButton.href;
        }
    });
};
